    .banner{
        position: relative;
        width: 100%;
        height: 500px;

        @media (max-width:767px) {
            height: 200px;
            
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: fill;

        }
    }

    .contact-text{
        width: 100%;
        height: auto;  

        .item{
            margin: .5rem 0;
            display: flex;
            gap:.5rem;
            font-size: 1rem;
            
            .icon{
                display: flex;
                justify-content: center;
                align-items: center;
            }

        }
    }

    

    .form-border{
        padding:1rem;
        border:5px solid #eee;
        border-radius: 10px;

        .heading{
            font-size: 1rem;
            font-weight: 500;
        }

        .form-button{
            padding: .5rem 1rem;
            color: #000;
            font-weight: 500;
            border-radius: 4px;
            text-transform: capitalize;
            background: linear-gradient(90deg, #f7971e 0%, #ffd200 100%);
            border: none;
            outline: none;
        }
    }
    .maplocation{
        width: 100%;
        height: 100%;
        padding:1rem .5rem;
        iframe{
            width: 100%;
            height: 100%;
        }
